  components: {
    Cabecera,
    Cuerpo,
    EstadisticaEntidad,
    Compartir,
    PieDePagina,
  },
  computed: {
      eleccion () {
          if (this.$route.params.eleccion === 'G') {
              return 'Gubernatura'
          } else if (this.$route.params.eleccion === 'D') {
              return 'Diputaciones'
          } else if (this.$route.params.eleccion === 'A') {
              return 'Ayuntamientos'
          }
      }
  }
